import React, { Component } from "react";
import "../styles/about.scss";
import Contactall from "./contactall";
import Partners from "./partners";
//import Servicehome from "./servicehome";
//import Servicehomemobile from "./servicehomemobile";
import Carous from "./carousel";

class Home extends Component {
  render() {
    const check1 = <i className="fa fa-check-circle circle2"></i>;
    return (
      <div>
        <Carous />
        <div className="home-body">
          <div className="aboutus">
            <h1
              className="aboutus-h1 wow flipInY"
              data-wow-delay="0.2s"
              data-wow-duration="2s"
            >
              ABOUT US
            </h1>
            <p
              className="aboutus-p wow fadeInUp"
              data-wow-delay="0.4s"
              data-wow-duration="2s"
            >
              BYG-SERVICES is dedicated to help small and medium size Africans
              companies import products from Turkey. Turkey is well-known with
              its large trading industry, best and high quality products.
              However, we understand that it is very hard for Africans importers
              to find reliable suppliers, good prices and quality. Therefore, we
              act as their representative and look after their interests. No
              matter the quantity of their orders, we take them seriouly, make
              sure that they get very competitive prices, and review quality
              control because we are looking for a long term business
              partnership.
            </p>
          </div>
          <div className="home-body-content">
            <div className=" tachyon-service ">
              <div className="br4  center">
                <div className="pa2 ph3-ns pb3-ns">
                  <h1
                    className="home-body-h1 wow fadeInUp"
                    data-wow-delay="0.6s"
                    data-wow-duration="2s"
                  >
                    {" "}
                    AREAS OF EXPERTISE
                  </h1>
                  <p
                    className="home-body-p wow fadeInUp"
                    data-wow-delay="0.8s"
                    data-wow-duration="2s"
                  >
                    We have partnered with reliable suppliers in various
                    sectors. Our areas of expertise include, but are not limited
                    to:
                  </p>
                  <div
                    className="d-flex flex-column flex-md-row justify-content-md-between  tachyon-check tl wow fadeInUp"
                    data-wow-delay="1s"
                    data-wow-duration="2s"
                  >
                    <div className="">
                      <p>{check1} Solar water heating systems</p>
                      <p>{check1} Solar PV systems</p>
                      <p> {check1} Cables</p>
                      <p> {check1} Iron &amp; Steel</p>
                      <p> {check1} Spare parts</p>
                    </div>
                    <div className="">
                      <p> {check1} Textile</p>
                      <p> {check1} Construction materials</p>
                      <p> {check1} Medical equipment</p>
                      <p> {check1} Agricultural Equipment</p>
                      <p> {check1} Flour</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/*<Servicehome /> 
        <Servicehomemobile />*/}

        <Partners />
        <Contactall />
      </div>
    );
  }
}

export default Home;
