import React, { Component } from "react";
import Carousel from "react-bootstrap/Carousel";
import container from "../images/container.jpg";
import warehouse1 from "../images/warehouse1.jpg";
import warehouse2 from "../images/container1.jpg";

class Carous extends Component {
  render() {
    return (
      <Carousel>
        <Carousel.Item>
          <img
            className="d-block im-size w-100"
            src={container}
            alt="First slide"
          />

          <Carousel.Caption className="caption">
            <h3 className="mb-5">
              We help small and medium size Africans companies import products
              from Turkey. We find reliable suppliers at competitive prices for
              them.
            </h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block im-size w-100"
            src={warehouse2}
            alt="Third slide"
          />

          <Carousel.Caption className="caption">
            <h3 className="mb-5">
              Are you an African company and looking for products to import from
              Turkey? BYG-SERVICES is ready to work with you. To meet customer
              requirements, we have partnered with many reliable suppliers.
            </h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block im-size w-100"
            src={warehouse1}
            alt="Third slide"
          />

          <Carousel.Caption className="caption">
            <h3 className="mb-5">
              We find best products at competitive prices, facilitate
              communication and negotiation. We also take care of documents,
              follow up production and delivery.
            </h3>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }
}

export default Carous;
