import React, { Component } from "react";
import "../styles/service.scss";
import Contactall from "./contactall";

class Service extends Component {
  state = {};
  render() {
    const check = <i className="fa fa-check-circle circle1"></i>;
    return (
      <div>
        <div className="container-fluid service tc">
          <h1 className="service-header">OUR SERVICES</h1>

          <div className="">
            <div className="tachyon-service">
              <div className="center">
                <div className="pa3 ph3-ns pb3-ns">
                  <div className="tc">
                    <p className="tl">
                      BYG-SERVICES acts as an intermediary between Africans
                      companies and Turkish manufacturers. Whenever you decide
                      to import products from Turkey, you first have to find a
                      supplier who manufacture the products you are looking for.
                      You also have to ensure that they are a manufacturer not a
                      trader. You always want to stay very competitive, ensure
                      quality and delivery time. How about having people in
                      Turkey who handle all these steps for you?
                    </p>
                  </div>
                  <div className="tachyon-check tl ">
                    <h1 className="process tc">SOURCING PROCESS</h1>
                    <p>The steps of our sourcing services are as follows:</p>
                    <div>
                      <p>
                        {check} Discuss with the client for products
                        specifications
                      </p>
                      <p>{check} Look for a suitable supplier</p>
                      <p> {check} Get quote from the supplier</p>
                      <p>
                        {check} Arrange production after confirmation from the
                        client
                      </p>
                      <p> {check} Oversee the production process</p>
                      <p> {check} Arrange factory visit for inspection</p>
                      <p> {check} Arrange shipment</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Contactall />
      </div>
    );
  }
}

export default Service;
